body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
}

img{
  border-radius: 50%;
}
input[type="text"] {
  width: 50%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  height: 2.8rem;
}


h3 {
  font-size: 1rem;

}


.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: auto;
  margin-top: 1rem;
  width: 50%;
  border: 3px;
  padding: 20px;
  text-align: center; 
  background-color: white;
  border-radius: 3%;
  font-weight: 90px;
  
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.guessP{
  font-weight: bolder;
}

.guessP2{
  font-weight: bolder;
  font-size: 1.2rem;
  color: cornflowerblue;
  
}

/* Add some padding inside the card container */
.container {
  align-items:center;
  padding: 2px 16px;
  text-align: center;
}

.unknownImg {
  width: 35%;

}

.search-bar {
  position: relative;
}

input[type="text"] {
  padding: 0.5em;
  border: 1px solid grey;
  width: 100%;
  font-weight: bolder;
}

.suggestions {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: white;
  border: 1px solid grey;
  width: 100%;
}

.suggestions li {
  padding: 0.5em;
}

.suggestions li:hover {
  background-color: lightgrey;
  cursor: pointer;
}

.circle {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  padding: 13px;
  margin-top: 0px;
  border: 3px solid rgb(255, 255, 255);
  color: white;
  text-align: center;
  font-size: 0.95rem;
  font-weight: bold;
}



.guess-container{
  justify-content: center;
  position: relative;
  text-align: center;
  display: flex;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  margin: 0 auto;
  width: 80%;
  align-content: center;
}

@media (min-width: 600px) {
  .circle {
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  .guess-container{
    grid-gap: 40px;
  }
  .GameFrame{
    max-width: 100%;
  }
}

.guesses{
  margin: 0;
  align-content: center;
}



.correct{
  background: green;
}

.false{
  background-color: red;
}

.guess-big{
  justify-content: center;
  align-content: center;
  position: relative;
  text-align: center;
  margin: 0 auto;
}

.ulGuesses{
  padding-top: 1  0px;
  padding-inline-start: 0px;
  align-content: center;
  list-style-type: none;
  font-weight: bolder;
}

.GameFrame{
  max-width: 100%;
  margin: auto;
}

@media (min-width: 600px) {
  .circle {
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  .guess-container{
    grid-gap: 40px;
  }
  .GameFrame{
    max-width: 80%;
  }
}

.fall-down-enter {
  transform: translateY(-100%);
}

.fall-down-enter-active {
  transform: translateY(0);
  transition: transform 4500ms ease-out;
}

.fall-down-exit {
  transform: translateY(0);
}

.fall-down-exit-active {
  transform: translateY(100%);
  transition: transform 4500ms ease-out;
}

.hid{
  visibility: hidden;
}

body{
  background-image: linear-gradient(to right, #0A2647 ,#205295)
}

.guessName {
  color: white;
}

.startButt {
  box-shadow: none;
  transition-duration: 0.4s;
  padding: 12px 28px;
  background-color: #205295;
  font-size: 1rem;
  color: white;
  border-radius: 5px;
  border-width: 2px;
  border-color: #205295;
  font-weight: bolder;
}

.startButt:hover {
  background-color: white; /* Green */
  color: white;
  color: #205295;
}

.listItem{
  font-weight: bold;
}
